@import url("https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@700&display=swap");
html, body {
  margin: 0;
  padding: 0;
  font-family: "Roboto Slab", serif;
  color: #fff;
}

a {
  text-decoration: none;
  color: #fff;
}

.app {
  width: 100%;
  min-height: 100vh;
  background: linear-gradient(0deg, rgb(61, 0, 90) 20%, rgb(173, 149, 176) 120%);
}

h1, h2, h3, h4, p {
  margin: 0;
}

._container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.px-1 {
  padding-left: 1rem;
  padding-right: 1rem;
}

.mt-2 {
  margin-top: 2rem;
}

.title {
  font-weight: 700;
  font-size: 2rem;
}

.subtitle {
  font-weight: 700;
  font-size: 1.2rem;
}

.navbar {
  box-shadow: 1px 10px 8px 0px rgba(34, 60, 80, 0.2);
  background-color: rgb(61, 0, 90);
}
.navbar .navbar__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo .logo__title {
  font-weight: 700;
  font-size: 24px;
  letter-spacing: 0.3rem;
}

.menu .menu__list {
  display: flex;
  justify-content: flex-end;
  margin: 1rem 0;
  padding: 0;
}
.menu .menu__item {
  list-style: none;
  margin-left: 10px;
}
.menu .menu__link {
  position: relative;
  text-decoration: none;
  color: #fff;
}
.menu .menu__link::before, .menu .menu__link::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  height: 2px;
  background-color: #fff;
  transform: scaleX(0);
  transition: transform 0.5s ease;
}
.menu .menu__link::before {
  top: 0;
  transform-origin: center right;
}
.menu .menu__link:hover::before {
  transform-origin: center left;
  transform: scaleX(1);
}
.menu .menu__link::after {
  bottom: -3px;
  transform-origin: center left;
}
.menu .menu__link:hover::after, .menu .menu__link.active::after {
  transform-origin: center right;
  transform: scaleX(1);
}

.home .home__title {
  text-align: center;
  margin-top: 3rem;
}
.home .home__inner {
  display: flex;
}
.home .home__inner-item {
  flex-basis: 30%;
  border: 2px dotted #fff;
  height: auto;
  margin: 2rem 5px;
}
.home .home__inner-item.hovered {
  border-color: rgb(142, 219, 48);
}
.home .home__inner-item-subtitle {
  text-align: center;
  margin-top: 1rem;
  text-transform: uppercase;
}
.home .home__inner-item-body {
  margin: 10px;
  min-height: 300px;
}

@media (max-width: 992px) {
  .home .home__inner {
    flex-wrap: wrap;
  }
  .home .home__inner-item {
    flex-basis: 45%;
  }
  .home .home__inner-item--past {
    order: 2;
    flex-basis: 100%;
  }
}
.launch-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  border: 2px solid #fff;
  margin: 10px 10px 15px;
  padding: 5px;
}
.launch-card:hover {
  box-shadow: 4px 4px 8px 7px rgba(220, 228, 234, 0.2);
  background-color: #8b4b94;
}
.launch-card .launch-card__image {
  height: 50px;
  width: auto;
}

.pagination {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 5px;
}
.pagination .pagination__button {
  cursor: pointer;
  padding: 7px 17px;
  border-radius: 15px;
  border: 2px solid #fff;
  background-color: #fff;
  color: #8b4b94;
  font-weight: 700;
  font-size: 1.2rem;
  opacity: 0.9;
  transition: all 0.2s ease-in-out;
}
.pagination .pagination__button:hover {
  opacity: 1;
}
.pagination .pagination__button:disabled {
  opacity: 0;
  cursor: auto;
}
.pagination .pagination__text {
  font-style: italic;
}

.launch {
  display: flex;
  justify-content: flex-start;
}
.launch .launch__image-block, .launch .launch__text-block {
  flex-basis: 30%;
}
.launch .launch__text-block {
  flex-basis: 70%;
}
.launch .launch__image-item {
  width: auto;
  height: 200px;
}

@media (max-width: 768px) {
  .launch {
    flex-wrap: wrap;
  }
  .launch .launch__title {
    text-align: center;
  }
  .launch .launch__image-block, .launch .launch__text-block {
    flex-basis: 100%;
    padding: 5px;
  }
  .launch .launch__image-block {
    display: flex;
    justify-content: center;
  }
}
.loader {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(0deg, rgb(61, 0, 90) 20%, rgb(173, 149, 176) 120%);
}
.loader .loader__inner {
  width: 3.5rem;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;
}
.loader .loader__inner-item {
  width: 0.8em;
  height: 0.8em;
  border-radius: 50%;
  background-color: #fff;
  transform: translateY(-100%);
  animation: wave 0.8s ease-in-out alternate infinite;
}
.loader .loader__inner-item:nth-of-type(1) {
  animation-delay: -0.4s;
}
.loader .loader__inner-item:nth-of-type(2) {
  animation-delay: -0.2s;
}

@keyframes wave {
  from {
    transform: translateY(-100%);
  }
  to {
    transform: translateY(100%);
  }
}